*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.5%;

    @include respond(tab-port) {
        font-size: 50%;
    }

    @include respond(phone) {
        font-size: 43.75%;
    }
}

body {
    box-sizing: border-box;
    font-family: "nunito-sans", sans-serif !important;
    line-height: 1.8;

    &>div {
        text-align: center;
    }
}